<!-- 动态通知函详情页word -->
<template>
  <div style="min-width:1100px;min-height:100%;">
    <!-- 标题按钮盒子 -->
    <div class="det-tip" style="position: fixed;width: 100%;height: 50px;background-color: #fff;">
      <p><i class="el-icon-chat-dot-square" style="margin-right:5px"></i>动态通知函详情</p>
      <el-button class="el-btn" size="medium" type="success" @click="exportt" style="margin-right: 35px;">导出为word</el-button>
    </div>
    <hr style="opacity:0.3">
    <!-- 正文盒子（只能使用内联样式） -->
    <div style="height:100%;background-color:#999;padding-top:100px;padding-bottom:50px">
      <div id="theWord" style="background-color: #fff;width: 1000px;margin: 0 auto;min-height: 1000px;">
        <div style="padding:20px 50px">
          <p style="text-align:center;font-size:44px;letter-spacing:-2px;line-height: 100px;">交通运输(战备)信息指挥中心</p>
          <!-- <p style="text-align:center;font-size:44px;letter-spacing:-2px;line-height: 100px;">增城区交通运输(战备)信息指挥中心</p> -->
        </div>
        <!-- 统计信息 -->
        <div style="padding:20px 100px">
          <p style="text-align:center;font-size:36px;line-height:48px;margin-bottom:50px">区交通运输（战备）信息指挥中心关于{{month.substring(0,4)}}年{{month.substring(5)}}月份重点营运车辆信息化动态监控情况的告知函</p>
          <p style="line-height:36px;">  
            <span style="font-size:22px;">局执法科：</span><br>
            <span style="font-size:22px;">&nbsp;&nbsp;经我中心监控平台监控，我区有关运输企业重点营运车辆{{month.substring(5)}}月份涉嫌存在如下违规行为：</span><br>
            <!-- 循环总的违规行为 -->
            <span v-for="(item, index) in tableData" style="margin-bottom:0px" :key="index">
              <br v-show="item.myindex !== 0">
              <span style="font-size:22px;" v-show="item.myindex !== 0">&nbsp;&nbsp;{{fullName(item.myindex)}}、 {{item.monitortype}}</span>
              <br v-show="item.myindex !== 0">
              <span style="font-size:22px;">&nbsp;&nbsp;（{{fullName(item.mysonindex)}}）{{item.hangyeid !== '-1'?item.hangye:'出租车'}}：</span>
              <!-- 循环宗数 -->
              <span style="font-size:22px;" v-for="(itemI, indexI) in item.yehustats" :key="indexI">
                <span>{{itemI.yehuname}} {{itemI.noticecount}} 宗；</span>
                <!-- 循环车牌（暂时隐藏） -->
                <!-- <span style="font-size:22px" v-for="(itemJ, indexJ) in itemI.vehiclestats" :key="indexJ">{{itemJ.plateno}}，</span> -->
              </span>
              <br>
              <span v-if="item.iftishi == true &&item.monitortypeflag == '1'" style="font-size:22px;">&nbsp;&nbsp;（以上涉嫌超速车辆详见附表1）</span>
              <span v-if="item.iftishi == true &&item.monitortypeflag == '2'" style="font-size:22px;">&nbsp;&nbsp;（以上疲劳驾驶车辆详见附表2）</span>
              <!-- <span v-if="item.iftishi == true &&item.monitortypeflag == '3'" style="font-size:22px;">&nbsp;&nbsp;（以上不按线路行驶车辆详见附表3）</span> -->
              <span v-if="item.iftishi == true &&item.monitortypeflag == '4'" style="font-size:22px;">&nbsp;&nbsp;（以上经常不在线具体车辆详见附表4）</span>
              <!-- <span v-if="item.iftishi == true &&item.monitortypeflag == '5'" style="font-size:22px;">&nbsp;&nbsp;（以上长期不在线具体车辆详见附表5）</span> -->
            </span>
            <br>
            <span style="font-size:22px;">&nbsp;&nbsp;上述运输公司重点营运车辆GPS动态情况违反了《广东省道路运输条例》和《道路运输车辆动态监督管理办法》（中华人民共和国交通运输部2016年第55号令）有关规定，请督促该公司立即整改。<br>&nbsp;&nbsp;专此函告<br>&nbsp;&nbsp;附表1：重点营运车辆涉嫌超速的车辆列表<br>&nbsp;&nbsp;附表2：重点营运车辆疲劳驾驶的车辆列表<br>&nbsp;&nbsp;附表3：客运班车涉嫌不按线路行驶违规的车辆列表<br>&nbsp;&nbsp;附表4：重点营运车辆经常不在线的车辆列表<br>&nbsp;&nbsp;附表5：重点营运车辆长期不在线的车辆列表</span>
          </p>
          <br><br><br><br><br>
          <p style="line-height:36px;text-align:right">  
            <span style="font-size:22px;">交通运输（战备）信息指挥中心</span><br>
            <!-- <span style="font-size:22px;">增城区交通运输（战备）信息指挥中心</span><br> -->
            <span style="font-size:22px;">&nbsp;&nbsp;年&nbsp;&nbsp;月&nbsp;&nbsp;日</span><br>
            <span style="font-size:22px;">联系电话：（62281345/62281348）</span><br>
          </p>
          <br><br><br><br><br>
        </div>
        <!-- 附表1：重点营运车辆涉嫌超速的车辆列表 -->
        <div style="padding:20px 100px">
          <p style="line-height:36px;font-size:22px;">附件：1</p>
          <p style="line-height:36px;text-align:center;font-size:22px;">重点营运车辆涉嫌超速的车辆列表</p>
          <!-- 附表1-1：重点营运车辆涉嫌超速的车辆列表（出租车专用表） -->
          <div v-if="THEtable1_1[0].length">
            <div v-for="(item,index) in THEtable1_1" :key="index">
              <br>
              <p style="line-height:36px;text-align:center;font-size:18px;">出租车涉嫌超速车辆列表</p>
              <table border="1" cellspacing="0" style="width:100%">
                <tr>
                  <th style="padding:10px">公司名称</th>
                  <th style="padding:10px">车牌</th>
                  <th style="padding:10px">超速平均速度</th>
                  <th style="padding:10px">超速开始时间</th>
                  <th style="padding:10px">超速总时长</th>
                </tr>
                <tr v-for="(item1,index1) in item" :key="index1" >
                  <td style="padding:10px" v-if="item1.therowspan_yehu" :rowspan="item1.therowspan_yehu">{{item1.myyehuname}}</td>
                  <td style="padding:10px" v-if="item1.therowspan_plate" :rowspan="item1.therowspan_plate">{{item1.myplateno}}<span v-if="item1.therowspan_plate&&item1.therowspan_plate>1">（{{item1.therowspan_plate}}宗）</span></td>
                  <td style="padding:10px">{{item1.speed_avg}} km/h</td>
                  <td style="padding:10px">{{item1.timeb}}</td>
                  <td style="padding:10px">{{item1.duration}}</td>
                </tr>
              </table>
            </div>
          </div>
          <p v-if="!THEtable1[0].length&&!THEtable1_1[0].length" style="color:#999;line-height:36px;text-align:center;font-size:22px;">无数据</p>
          <div v-if="THEtable1[0].length">
            <div v-for="(item,index) in THEtable1" :key="index">
              <br>
              <p style="line-height:36px;text-align:center;font-size:18px;">{{item[0].hangye}}涉嫌超速车辆列表</p>
              <table border="1" cellspacing="0" style="width:100%">
                <tr>
                  <th style="padding:10px">公司名称</th>
                  <th style="padding:10px">车牌</th>
                  <th style="padding:10px">超速平均速度</th>
                  <th style="padding:10px">超速开始时间</th>
                  <th style="padding:10px">超速总时长</th>
                </tr>
                <tr v-for="(item1,index1) in item" :key="index1" >
                  <td style="padding:10px" v-if="item1.therowspan_yehu" :rowspan="item1.therowspan_yehu">{{item1.myyehuname}}</td>
                  <td style="padding:10px" v-if="item1.therowspan_plate" :rowspan="item1.therowspan_plate">{{item1.myplateno}}<span v-if="item1.therowspan_plate&&item1.therowspan_plate>1">（{{item1.therowspan_plate}}宗）</span></td>
                  <td style="padding:10px">{{item1.speed_avg}} km/h</td>
                  <td style="padding:10px">{{item1.timeb}}</td>
                  <td style="padding:10px">{{item1.duration}}</td>
                </tr>
              </table>
            </div>
          </div>
        </div>
        <!-- 附表2：涉嫌疲劳驾驶违规的车辆列表 -->
        <div style="padding:20px 100px">
          <p style="line-height:36px;font-size:22px;">附件：2</p>
          <p style="line-height:36px;text-align:center;font-size:22px;">涉嫌疲劳驾驶违规的车辆列表</p>            
          <p v-if="!table2.length" style="color:#999;line-height:36px;text-align:center;font-size:22px;">无数据</p>
          <table border="1" cellspacing="0" v-if="table2.length" style="width:100%">
            <tr>
              <th style="padding:10px">公司名称</th>
              <th style="padding:10px">行业</th>
              <th style="padding:10px">车牌</th>
              <th style="padding:10px">辖区</th>
              <th style="padding:10px">监控类型</th>
              <th style="padding:10px">下发时间</th>
            </tr>
            <tr v-for="(item,index) in table2" :key="index">
              <td style="padding:10px" >{{item.yehuname}}</td>
              <td style="padding:10px" >{{item.hangye}}</td>
              <td style="padding:10px" >{{item.plateno}}</td>
              <td style="padding:10px" >增城</td>
              <td style="padding:10px" >疲劳驾驶</td>
              <td style="padding:10px" >{{item.timeb}}</td>
            </tr>
          </table>
        </div>
        <!-- 附表3：客运班车涉嫌不按线路行驶违规的车辆列表 -->
        <div style="padding:20px 100px">
          <p style="line-height:36px;font-size:22px;">附件：3</p>
          <p style="line-height:36px;text-align:center;font-size:22px;">客运班车涉嫌不按线路行驶违规的车辆列表</p>
            <p v-if="!table3.length" style="color:#999;line-height:36px;text-align:center;font-size:22px;">无数据</p>
            <table border="1" cellspacing="0" v-if="table3.length" style="width:100%">
              <tr>
                <th style="padding:10px">公司名称</th>
                <th style="padding:10px">行业</th>
                <th style="padding:10px">车牌</th>
                <th style="padding:10px">辖区</th>
                <th style="padding:10px">监控类型</th>
                <th style="padding:10px">下发时间</th>
              </tr>
              <tr v-for="(item,index) in table3" :key="index">
                <td style="padding:10px">{{item.yehuname}}</td>
                <td style="padding:10px">{{item.hangye}}</td>
                <td style="padding:10px">{{item.plateno}}</td>
                <td style="padding:10px">增城</td>
                <td style="padding:10px">不按线路行驶</td>
                <td style="padding:10px">{{item.timeb}}</td>
              </tr>
            </table>
        </div>
        <!-- 附表4：重点营运车辆经常不在线的车辆列表 -->
        <div style="padding:20px 100px">
          <p style="line-height:36px;font-size:22px;">附件：4</p>
          <p style="line-height:36px;text-align:center;font-size:22px;">重点营运车辆经常不在线的车辆列表</p>
          <p v-if="!table4.length" style="color:#999;line-height:36px;text-align:center;font-size:22px;">无数据</p>
          <table border="1" cellspacing="0" v-if="table4.length" style="width:100%">
            <tr>
              <th style="padding:10px;width:15%">行业</th>
              <th style="padding:10px;width:28%">公司名称</th>
              <th style="padding:10px;width:57%" colspan="7">车牌号</th>
            </tr>
            <div style="display: table-row-group" v-for="(item,index) in table4" :key="index">
              <tr v-for="(item1,index1) in item.thevehiclestats" :key="index1">
                <td v-if="index1 == 0&&item.therowspan_hangye" style="padding:10px;width:15%;vertical-align:middle;" :rowspan="item.therowspan_hangye">{{item.myhangye}}</td>
                <td v-if="index1 == 0" style="padding:10px;width:28%;vertical-align:middle;" :rowspan="item.therowspan_company">{{item.yehuname}}</td>
                <td style="padding:10px;" v-for="(item2,index2) in item.thevehiclestats[index1]" :key="index2">{{item2.plateno}}</td>
              </tr>
            </div>
          </table>
          <p>注：经常不在线：同一车辆连续30天内累计10天单车日在线率为零，则视为该车辆经常不在线。长期不在线：同一车辆连续90天内累计30天单车日在线率为零，则视为该车辆经常不在线。</p>
        </div>
        <!-- 附表5：重点营运车辆长期不在线的车辆列表 -->
        <div style="padding:20px 100px">
          <p style="line-height:36px;font-size:22px;">附件：5</p>
          <p style="line-height:36px;text-align:center;font-size:22px;">重点营运车辆长期不在线的车辆列表</p>
          <p v-if="!table5.length" style="color:#999;line-height:36px;text-align:center;font-size:22px;">无数据</p>
          <table border="1" cellspacing="0" v-if="table5.length" style="width:100%">
            <tr>
              <th style="padding:10px;width:15%">行业</th>
              <th style="padding:10px;width:28%">公司名称</th>
              <th style="padding:10px;width:57%" colspan="7">车牌号</th>
            </tr>
            <div style="display: table-row-group" v-for="(item,index) in table5" :key="index">
              <tr v-for="(item1,index1) in item.thevehiclestats" :key="index1">
                <td v-if="index1 == 0&&item.therowspan_hangye" style="padding:10px;width:15%;vertical-align:middle;" :rowspan="item.therowspan_hangye">{{item.myhangye}}</td>
                <td v-if="index1 == 0" style="padding:10px;width:28%;vertical-align:middle;" :rowspan="item.therowspan_company">{{item.yehuname}}</td>
                <td style="padding:10px;" v-for="(item2,index2) in item.thevehiclestats[index1]" :key="index2">{{item2.plateno}}</td>
              </tr>
            </div>
          </table>
          <p>注：经常不在线：同一车辆连续30天内累计10天单车日在线率为零，则视为该车辆经常不在线。长期不在线：同一车辆连续90天内累计30天单车日在线率为零，则视为该车辆经常不在线。</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import '@/assets/less/custom.less';// 样式文件
  import HttpServer from '@/requset/http' // axios请求
  import '@/assets/js/new/jquery-3.3.1.min.js'
  import '@/assets/js/new/FileSaver.js'
  import '@/assets/js/new/jquery.wordexport.js'
  // import '../../../static/VideoMonitor/js/jquery-3.3.1.min.js'
  // import '../../../static/VideoMonitor/js/FileSaver.js'
  // import '../../../static/VideoMonitor/js/jquery.wordexport.js'
  
	export default {
		data() {
			return {
        month:'',// 月份
        tableData: '',// 总数据
        time_b: '',// 开始时间
        time_e: '',// 结束时间

        table1:'',// 附表1：超速（处理前）
        table1_1:'',// 附表1-1：超速(出租车)（处理前）
        THEtable1: [[]],// 附表1：超速（处理后）
        THEtable1_1: [[]],// 附表1：超速(出租车)（处理后）
        table2: [],// 附表2：疲劳驾驶（处理前）
        table3: [],// 附表3：不按路线行驶（处理前）
        table4: [],// 附表4：重点营运车辆经常不在线（处理前）
        table5: [],// 附表5：重点营运车辆长期不在线（处理前）

        // 阿拉伯数字转化
        chnNumChar: ["零","一","二","三","四","五","六","七","八","九"],
        chnUnitSection: ["","万","亿","万亿","亿亿"],
        chnUnitChar: ["","十","百","千"],
			}
		},
		computed: {
    },
    computed: {
      fullName: function () {
        return function(index) {
          return this.NumberToChinese(index)
        }
      }
    },
		methods: {
      // 查询动态告知函——监控统计
      getDet(){
        if( this.month === null && this.month === ""){
          this.$message({
            message: '日期为空',
            type: 'warning'
          });
          return;
        } 
        HttpServer.post("/monitorApi/api/v1/DynamicCheck/statDynamicNotice", {
          month:this.month
        })
        .then(res => {
          if(res.data.length>0){
            console.log('查询')
            console.log(res);
            // 渲染数据
            this.tableData = res.data
            // 处理tableData的数据
            // 遍历处理tableData中的 数字转成相应的文字
            let _this = this
            let i = 1;// 大标题的序号
            let j = 1;// 小标题的序号
            this.tableData.map(function(item,index){
              // 把重复的标题去掉
              // 第一项
              if(index == 0){
                item.myindex = i
                i++
                item.mysonindex = j
                j++
              }
              // 标题重复
              else if(index>0&&item.monitortypeflag == _this.tableData[index-1].monitortypeflag){
                item.monitortype = ""
                item.myindex = 0
                item.mysonindex = j
                j++
              }
              // 非重复项
              else{
                _this.tableData[index-1].iftishi = true
                item.myindex = i
                i++
                j = 1
                item.mysonindex = j
                j++
              }
              // 第一次处理附件四（gps经常不在线）的数据：把行业从外层传到里层
              if(item.monitortypeflag == '4'){
                item.yehustats.map(function(item1,index1){
                  // 把行业传过来
                  item1.thehangye = item.hangye
                  // 解决浅拷贝问题
                  let a = {}
                  a = item1
                  //_this.$set(_this.table4,_this.table4.length,a)
                  _this.table4.push(JSON.parse(JSON.stringify(a)))
                })
              }
              // 第一次处理附件五（gps长期不在线）的数据：把行业从外层传到里层
              if(item.monitortypeflag == '5'){
                item.yehustats.map(function(item1,index1){
                  // 把行业传过来
                  item1.thehangye = item.hangye
                  // 解决浅拷贝问题
                  let a = {}
                  a = item1
                  _this.table5.push(JSON.parse(JSON.stringify(a)))
                })
              }
            })
            this.tableData[this.tableData.length-1].iftishi = true
            console.log('打印处理后的tableData统计数据')
            console.log(this.tableData)
            console.log('打印未处理的表格附件table4内容')
            console.log(this.table4)
            console.log('打印未处理的表格附件table5内容')
            console.log(this.table5)
            // 调用进一步处理附件四和附件五的方法
            if(this.table4.length>0){
              this.changetable4()
            }
            if(this.table5.length>0){
              this.changetable5()
            }
          }
        })
        .catch(err => {
          console.log("失败");
        });
      },
      // 附表1：查询超速车辆列表
      getSpeedRecords(){
        HttpServer.post("/monitorApi/api/v1/DynamicCheck/querySpeedRecords", {
            time_b: this.time_b,
            time_e: this.time_e,
            vid:'',
            hangyeid: '',//this.hangyeid,
            yehuid: '',//this.yehuid,
        })
        .then(res => {
          if(res.data.length>0){
            console.log('打印未处理的表格附件table1内容')
            console.log(res);
            this.table1 = res.data
            // 第一步，处理table1的数据，把同个行业的放在同个表格里（处理到THEtable1中）
            let i = 0
            let j = 0
            let _this = this
            this.table1.map(function(item,index){
              // 第一项
              if(index == 0){
                _this.THEtable1[i][j] = item
              }
              // 行业id相同
              else if(item.hangyeid == _this.table1[index-1].hangyeid){
                j++
                _this.THEtable1[i][j] = item
              }
              // 行业id不同
              else{
                i++;
                _this.THEtable1[i] = []
                j = 0
                _this.THEtable1[i][j] = item
              }
            })
            // 第二步，处理THEtable1里面的车牌号，把相同车牌的单元格合并（plateno --> myplateno）
            this.THEtable1.map(function(item,index){
              let k = 1// 用于多少车辆的标识符
              let q = 0// 目前比较的是第几项
              for(var i=0;i<item.length;i++){
                // 表格第一项
                if(i == 0){
                  item[i].myplateno = item[i].plateno
                }
                // 车牌相同
                else if(i !== 0&&item[i].plateno == item[i-1].plateno){
                  item[i].myplateno = ''
                  k++
                }
                // 车牌不同
                else{
                  item[i].myplateno = item[i].plateno
                  item[q].therowspan_plate = k
                  q = i
                  k = 1
                }
              }
              // for循环结束以后处理最后一个车牌项
              item[q].therowspan_plate = k
            })
            // 第三步，处理THEtable1里面的公司名，把相同公司的单元格合并（yehuname --> myyehuname）
            this.THEtable1.map(function(item,index){
              let k = 1// 用于多少公司的标识符
              let q = 0// 目前比较的是第几项
              for(var i=0;i<item.length;i++){
                // 表格第一项
                if(i == 0){
                  item[i].myyehuname = item[i].yehuname
                }
                // 车牌相同
                else if(i !== 0&&item[i].yehuname == item[i-1].yehuname){
                  item[i].myyehuname = ''
                  k++
                }
                // 车牌不同
                else{
                  item[i].myyehuname = item[i].yehuname
                  item[q].therowspan_yehu = k
                  q = i
                  k = 1
                }
              }
              // for循环结束以后处理最后一个车牌项
              item[q].therowspan_yehu = k
            })
            console.log('打印处理完的表格附件THEtable1内容')
            console.log(this.THEtable1)
            console.log('3、mounted')
            this.$forceUpdate()
          }
        })
        .catch(err => {
          console.log("失败");
        });
      },
      // 附表1-1：查询超速车辆列表(返回数据跟上面完全一样)
