            this.table1_1 = res.data
            // 第一步，处理table1的数据，把同个行业的放在同个表格里（处理到THEtable1中）
            let i = 0
            let j = 0
            let _this = this
            this.table1_1.map(function(item,index){
              // 第一项
              if(index == 0){
                _this.THEtable1_1[i][j] = item
              }
              // 行业id相同
              else if(item.hangyeid == _this.table1_1[index-1].hangyeid){
                j++
                _this.THEtable1_1[i][j] = item
              }
              // 行业id不同
              else{
                i++;
                _this.THEtable1_1[i] = []
                j = 0
                _this.THEtable1_1[i][j] = item
              }
            })
            // 第二步，处理THEtable1里面的车牌号，把相同车牌的单元格合并（plateno --> myplateno）
            this.THEtable1_1.map(function(item,index){
              let k = 1// 用于多少车辆的标识符
              let q = 0// 目前比较的是第几项
              for(var i=0;i<item.length;i++){
                // 表格第一项
                if(i == 0){
                  item[i].myplateno = item[i].plateno
                }
                // 车牌相同
                else if(i !== 0&&item[i].plateno == item[i-1].plateno){
                  item[i].myplateno = ''
                  k++
                }
                // 车牌不同
                else{
                  item[i].myplateno = item[i].plateno
                  item[q].therowspan_plate = k
                  q = i
                  k = 1
                }
              }
              // for循环结束以后处理最后一个车牌项
              item[q].therowspan_plate = k
            })
            // 第三步，处理THEtable1里面的公司名，把相同公司的单元格合并（yehuname --> myyehuname）
            this.THEtable1_1.map(function(item,index){
              let k = 1// 用于多少公司的标识符
              let q = 0// 目前比较的是第几项
              for(var i=0;i<item.length;i++){
                // 表格第一项
                if(i == 0){
                  item[i].myyehuname = item[i].yehuname
                }
                // 车牌相同
                else if(i !== 0&&item[i].yehuname == item[i-1].yehuname){
                  item[i].myyehuname = ''
                  k++
                }
                // 车牌不同
                else{
                  item[i].myyehuname = item[i].yehuname
                  item[q].therowspan_yehu = k
                  q = i
                  k = 1
                }
              }
              // for循环结束以后处理最后一个车牌项
              item[q].therowspan_yehu = k
            })
            console.log('打印处理完的表格附件THEtable1内容')
            console.log(this.THEtable1_1)
            console.log('3、mounted')
            this.$forceUpdate()
          }
        })
        .catch(err => {
          console.log("失败");
        });
