      },
      // 第二次处理附表4（把第一列行业的相同项目合并，同时遍历最后一列车牌号，每7个为一组，如果超过七个，还要增加行业列的rowspan）
      changetable4(){
        console.log('处理4444')
        let _this = this
        let k = 1// 用于多少公司的标识符
        let q = 0// 目前比较的是第几项
        let g = 0// 单条数据车牌每超过7条时，行业的rowspan要多加1
        this.table4.map(function(item,index){
          // 1. 通过车牌数量判断公司名称列的行数
          // 把行数赋值给公司名称:therowspan_company(公司列的行数)
          item.therowspan_company = Math.ceil(item.vehiclestats.length/7)
          // 如果这条数据超过7个车牌，那行业就会受影响
          if(item.therowspan_company>1){
            g = g + item.therowspan_company-1
          }
          // 表格第一项
          if(index == 0){
            // console.log('第一项')
            item.myhangye = item.thehangye
          }
          // 行业相同
          else if(index !== 0&&item.thehangye == _this.table4[index-1].thehangye){
            // console.log('行业相同')
            item.myhangye = ''
            k++
          }
          // 行业不同
          else{
            item.myhangye = item.thehangye
            _this.table4[q].therowspan_hangye = k + g
            q = index
            k = 1
            g = 0
          }
        })
        // for循环结束以后处理最后一个车牌项
        this.table4[q].therowspan_hangye = k + g
        // 进一步处理附表4
        this.changetable4Again()
      },
      // 第三次处理附表4（把每条数据的车牌条数补全为7的倍数，然后把数组切成二维数组，每七个一个小数组）
      changetable4Again(){
        console.log('进一步处理附表4')
        console.log(this.table4)
        this.table4.map(function(item,index){
          // 第一次把vehiclestats数组项补全到7的倍数
          let b = []
          switch(item.vehiclestats.length%7) {
            case 0:
              b = []
              break;
            case 1:
              b = [{},{},{},{},{},{}]
              break;
            case 2:
              b = [{},{},{},{},{}]
              break;
            case 3:
              b = [{},{},{},{}]
              break;
            case 4:
              b = [{},{},{}]
              break;
            case 5:
              b = [{},{}]
              break;
            case 6:
              b = [{}]
              break;
          } 
          item.vehiclestats = item.vehiclestats.concat(b)
          // 第二次把每7项合成一个小数组，分给thevehiclestats
          item.thevehiclestats = []
          for(let i=0;i<Math.ceil(item.vehiclestats.length/7);i++){
            item.thevehiclestats[i] = item.vehiclestats.slice(i*7+0,i*7+7)
          }
        })
      },
      // 第二次处理附表5（把第一列行业的相同项目合并，同时遍历最后一列车牌号，每7个为一组，如果超过七个，还要增加行业列的rowspan）
      changetable5(){
        console.log('处理5555')
        console.log(this.table5)
        let _this = this
        let k = 1// 用于多少公司的标识符
        let q = 0// 目前比较的是第几项
        let g = 0// 单条数据车牌每超过7条时，行业的rowspan要多加1
        this.table5.map(function(item,index){
          // 1. 通过车牌数量判断公司名称列的行数
          // 把行数赋值给公司名称:therowspan_company(公司列的行数)
          item.therowspan_company = Math.ceil(item.vehiclestats.length/7)
          // 如果这条数据超过7个车牌，那行业就会受影响
          if(item.therowspan_company>1){
            g = g + item.therowspan_company-1
          }
          // 表格第一项
          if(index == 0){
            // console.log('第一项')
            item.myhangye = item.thehangye
          }
          // 行业相同
          else if(index !== 0&&item.thehangye == _this.table5[index-1].thehangye){
            // console.log('行业相同')
            item.myhangye = ''
            k++
          }
          // 行业不同
          else{
            item.myhangye = item.thehangye
            _this.table5[q].therowspan_hangye = k + g
            q = index
            k = 1
            g = 0
          }
        })
        // for循环结束以后处理最后一个车牌项
        this.table5[q].therowspan_hangye = k + g
        // 进一步处理附表45
        this.changetable5Again()
      },
      // 第三次处理附表5（把每条数据的车牌条数补全为7的倍数，然后把数组切成二维数组，每七个一个小数组）
      changetable5Again(){
        console.log('进一步处理附表5')
        console.log(this.table5)
        this.table5.map(function(item,index){
          // 第一次把vehiclestats数组项补全到7的倍数
          let b = []
          switch(item.vehiclestats.length%7) {
            case 0:
              b = []
              break;
            case 1:
              b = [{},{},{},{},{},{}]
              break;
            case 2:
              b = [{},{},{},{},{}]
              break;
            case 3:
              b = [{},{},{},{}]
              break;
            case 4:
              b = [{},{},{}]
              break;
            case 5:
              b = [{},{}]
              break;
            case 6:
              b = [{}]
              break;
          } 
          item.vehiclestats = item.vehiclestats.concat(b)
          // 第二次把每7项合成一个小数组，分给thevehiclestats
          item.thevehiclestats = []
          for(let i=0;i<Math.ceil(item.vehiclestats.length/7);i++){
            item.thevehiclestats[i] = item.vehiclestats.slice(i*7+0,i*7+7)
          }
        })
      },
      // 导出为word
      exportt(){
        console.log('导出')
        $("#theWord").wordExport();
      },
      // 阿拉伯数字转汉字
      SectionToChinese(section){
        var strIns = '', chnStr = '';
        var unitPos = 0;
        var zero = true;
        while(section > 0){
          var v = section % 10;
          if(v === 0){
            if(!zero){
              zero = true;
              chnStr = this.chnNumChar[v] + chnStr;
            }
          }else{
            zero = false;
            strIns = this.chnNumChar[v];
            strIns += this.chnUnitChar[unitPos];
            chnStr = strIns + chnStr;
          }
          unitPos++;
          section = Math.floor(section / 10);
        }
        return chnStr;
      },
      // 阿拉伯数字转汉字
      NumberToChinese(num){
        var unitPos = 0;
        var strIns = '', chnStr = '';
        var needZero = false;
        if(num === 0){
          return this.chnNumChar[0];
        }
        while(num > 0){
          var section = num % 10000;
          if(needZero){
            chnStr = this.chnNumChar[0] + chnStr;
          }
          strIns = this.SectionToChinese(section);
          strIns += (section !== 0) ? this.chnUnitSection[unitPos] : this.chnUnitSection[0];
          chnStr = strIns + chnStr;
          needZero = (section < 1000) && (section > 0);
          num = Math.floor(num / 10000);
          unitPos++;
        }
        return chnStr;
      },
      // 获取某月的天数
      getDaysOfMonth(year,month){
        var date=new Date(year,month,0);
        var days=date.getDate();
        return days;
      }
    },
    created () {
      console.log('1、created')
      // 获取url中的month
      this.month = this.$route.params.month
      // 处理月份数据
      this.time_b = this.month + "-01" + ' 00:00:00'
      this.time_e = this.month + "-" + this.getDaysOfMonth(this.month.substring(0,4),this.month.substring(5)) + ' 23:59:59'
      // 查询统计信息
      this.getDet()
      // 查询附表1：超速
      this.getSpeedRecords()
      // 查询附表1-1：超速-出租
      this.getChuzuRecords()
      // 查询附表2：疲劳驾驶
      this.getFatigueRecords()
      // 查询附表3：不按线路行驶
      this.getRouteCheckRecords()
    },
    mounted () {
      console.log('2、mounted')
      this.$forceUpdate()
    }
	}
</script>
