<!-- 1-5客运线路管理 -->
<template>
  <div style="height:100%">
    <div class="top-box">
      <div class="top-inputs-box">
          <span>公司代码</span>
          <el-input class="el-input" size="medium" placeholder="请输入公司代码"></el-input>
          <span>行业代码</span>
          <el-input class="el-input" size="medium" placeholder="请输入行业代码"></el-input>
          <span>场站名称</span>
          <el-input class="el-input" size="medium" placeholder="请输入场站名称"></el-input>
          <el-button class="el-btn" size="medium" type="success" @click="removeEnclosure">清除围栏</el-button>
          <el-button class="el-btn" size="medium" type="success" @click="addEnclosure">增加围栏</el-button>
          <el-button class="el-btn" size="medium" type="success" @click="submitEnclosure">提交</el-button>
      </div>
    </div>
    <div class="map-body-box">
      <div id="GDMap1_5"></div>
    </div>
  </div>
</template>
<script>
  import '@/assets/less/custom.less';
  import loadMap from "@/utils/loadMap";
	export default {
		data() {
			return {
        /** 地图构造数据 **/
        GDMap1_5:null,// 地图实例
        plugins: ["AMap.MarkerClusterer","AMap.PolyEditor","AMap.MouseTool"],// 高德的一些插件// 更多参考：https://lbs.amap.com/api/javascript-api/guide/abc/plugins#plugins
        key: "c5eac55551560531336988396dacbf53",// key
        v: "1.4.14",// 地图版本
        mouseTool: null,// 鼠标工具实例
        
        /** 地图数据 **/
        overlays:[],// 地图上的覆盖物
        path: [// 面积描边
          // [116.169465,39.932670],
          // [116.160260,39.924492], 
          // [116.150625,39.710019],
          // [116.183198,39.709920], 
          // [116.226950,39.777616], 
          // [116.442621,39.799892],
          // [116.463478,39.790066], 
          // [116.588276,39.809551],
          // [116.536091,39.808859],
          // [116.573856,39.839643], 
          // [116.706380,39.916740],
          // [116.600293,39.937770],
          // [116.514805,39.982375],
          // [116.499935,40.013710],
          // [116.546520,40.030443], 
          // [116.687668,40.129961], 
          // [116.539697,40.080659],
          // [116.503390,40.058474],
          // [116.468800,40.052578]
        ],
        path1: [// 初始多边形的坐标数据
          [113.200637, 23.045178],
          [113.200637, 23.205178],
          [113.360637, 23.205178],
          [113.360637, 23.045178],
        ],
      }
		},
		computed: {
		},
		methods: {
      // 提交围栏
      submitEnclosure(){
        console.log('提交围栏')
        if(this.GDMap1_5.getAllOverlays('polygon').length >0){
          console.log(this.GDMap1_5.getAllOverlays('polygon')[0].B.path)
        }
        else{
          this.$message.warning('请先画围栏')
        }
      },
      // 清除围栏
      removeEnclosure(){
        console.log('清除围栏')
        this.GDMap1_5.clearMap();
      },
      // 增加围栏
      addEnclosure(){
        // 如果地图上有多边形了
        if(this.GDMap1_5.getAllOverlays('polygon').length>0){
          this.$message.warning('已存在围栏，请先清除！');
        }
        // 如果地图上没有多边形
        else{
          let _this = this
          // 开启画图功能
          var mouseTool = new AMap.MouseTool(this.GDMap1_5)
          // 画完后的事件
          mouseTool.on('draw',function(e){
            console.log(e)
            // 把画的多边形加到overlays数组里
            _this.$set(_this.overlays,_this.overlays.length,e.obj)
            // 关闭绘制事件
            mouseTool.close();
            // 开启编辑事件
            var polyEditor = new AMap.PolyEditor(_this.GDMap1_5, _this.GDMap1_5.getAllOverlays('polygon')[0])
            polyEditor.open()
          })
          mouseTool.polygon(); 
        }
      }
    },
    mounted () {
      // 渲染高德地图
      loadMap(this.key, this.plugins, this.v)
        .then(resMAP => {
          // 参数准备
          console.log('地图加载成功')
          let _this = this
          // 构造地图
          this.GDMap1_5 = new AMap.Map("GDMap1_5", {
            zoom: 15,
            center: [113.83, 23.30]
          });
        })
        .catch(() => {
          //this.loading = false;
          console.log("地图加载失败！");
        });
    }
	}

</script>
<style scoped>
@import "../../assets/less/gy.less";
#GDMap1_5{
  height:100%;
}
</style>


